@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  font-family: Arial, Helvetica, sans-serif;
}
* {
  box-sizing: border-box;
}

/* Full-width input fields */
.entry {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}
TextareaAutosize {
  width: 100px;
  padding: 15px;
  margin: 5px 0 22px 20px;
  display: inline-block;
  border-radius: 4px;
  background-color: #b69b9b;
  border: none;
}

/* Add a background color when the inputs get focus */
input[type="text"]:focus,
input[type="datetime-local"]:focus {
  background-color: #ddd;
  outline: none;
}

/* Float cancel and signup buttons and add an equal width */
.cancelbtn,
.savebtn {
  padding: 14px 20px;
  margin: 8px 0;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  float: left;
  width: 50%;
}

/* Add padding to container elements */
.container {
  padding: 16px;
}

/* The Modal (background) */
.eventForm {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: #474e5d;
  padding-top: 50px;
}

/* Modal Content/Box */
.eventForm-content {
  background-color: #fefefe;
  margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
  /* border: 1px solid #888; */
  width: 80%; /* Could be more or less, depending on screen size */
}

/* Style the horizontal ruler */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

.checked {
  margin-bottom: 15px;
}
/* The Close Button (x) */
.close {
  position: absolute;
  right: 60px;
  top: 160px;
  font-size: 40px;
  font-weight: bold;
  color: green;
}

.close:hover,
.close:focus {
  color: #f44336;
  cursor: pointer;
}

/* Clear floats */
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

/* Change styles for cancel button and signup button on extra small screens */
@media screen and (max-width: 400px) {
  .cancelbtn,
  .savebtn {
    width: 100%;
  }
  .entry {
    width: 65%;
  }
  TextareaAutosize {
    width: 50px;
  }
}

.empty h1 {
  margin-top: 50px;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 300px;
  font-size: 30px;
}
.emptypage {
  text-align: center;
  font-size: 25px;
  /* font-family: "Merriweather", cursive; */
  font-weight: 300px;
}

/* search bar */
div .search {
  position: relative;
  margin-left: 100px;
  width: 130px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  background-color: white;
  background-image: url("https://img.icons8.com/cute-clipart/64/000000/search-more.png");
  background-position: 10px 10px;
  background-repeat: no-repeat;
  background-size: 32px;
  padding: 12px 20px 12px 40px;
  transition: width 0.4s ease-in-out;
}

input[type="text"]:focus {
  width: 350px;
}
@media screen and (max-width: 400px) {
  div .search {
    margin-left: 50px;
  }
  input[type="text"]:focus {
    width: 200px;
  }
}

/* card layout */
.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.card {
  border: 1px solid #000;
  text-align: center;
  margin: 10px;
  padding: 10px 3px;
}

.HomePage {
  align-content: center;
}
.HomePage h1 h3 {
  font-family: "Lobster", cursive;
  position: relative;
  text-align: center;
  bottom: 0;
  width: 100%;
  font-weight: 900px;
}
.HomePage h1 {
  font-size: 30px;
}

.HomePage h3 {
  font-size: 20px;
}

